<!-- auth@davi -->
<template>
  <div id='scroll-orderMonth'>
    <!-- top -->
    <van-tabs
      v-model="active"
      title-active-color='#ff7d41'
    >
      <van-tab title="现在入住"></van-tab>
      <van-tab title="预留订单"></van-tab>
    </van-tabs>
    <!-- center -->
    <div class='mt10'>
      <van-form>
        <div class="flex">
          <van-field
            class="w30"
            name="radio"
            label="租赁方式"
          />
          <van-radio-group
            v-model="dataForm.orderType"
            class="w70"
            style="display:flex;background:#fff;"
          >
            <van-radio
              name="3"
              style="margin-right:50px;"
            >长租房</van-radio>
            <van-radio name="4">短租房</van-radio>
          </van-radio-group>
        </div>
        <div
          class="flex"
          @click="show_type('客户类型')"
        >
          <van-field
            class="w30"
            label="客户类型"
          />
          <div class="w60">{{ customerType }}</div>
          <van-icon
            class="w10"
            color='#b2b2b2'
            name="arrow"
          />
        </div>
        <div
          v-if="customerType == '协议单位'"
          class="flex"
          @click="show_type('单位名称')"
        >
          <van-field
            class="w30"
            label="单位名称"
          />
          <div class="w60">{{ unitName }}</div>
          <van-icon
            class="w10"
            color='#b2b2b2'
            name="arrow"
          />
        </div>
        <div class="title">租赁信息</div>
        <!-- 1.起租日期 -->
        <div
          v-if="active == '1' "
          class="flex"
          @click="show_stime('start')"
        >
          <van-field
            class="w30"
            label="起租日期"
          />
          <div class="w60">{{ dataForm.startTime }}</div>
          <van-icon
            class="w10"
            color='#b2b2b2'
            name="arrow"
          />
        </div>
        <div
          class="flex"
          @click="show_type('基础房型')"
        >
          <van-field
            class="w30"
            label="基础房型"
          />
          <div
            class="w60"
            :style="{color:(virScaleName == '请选择基础房型'?'#989898':'#000')}"
          >{{ virScaleName }}</div>
          <van-icon
            class="w10"
            color='#b2b2b2'
            name="arrow"
          />
        </div>
        <div
          class="flex"
          @click="show_type('产品房型')"
        >
          <van-field
            class="w30"
            label="产品房型"
          />
          <div
            class="w60"
            :style="{color:(proScaleName == '请选择产品房型'?'#989898':'#000')}"
          >{{ proScaleName }}</div>
          <van-icon
            class="w10"
            color='#b2b2b2'
            name="arrow"
          />
        </div>
        <div
          v-if="active == '0'"
          class="flex"
          @click="show_type('房间号')"
        >
          <van-field
            class="w30"
            label="房间号"
          />
          <div
            class="w60"
            :style="{color:(roomSn == '请选择房间号'?'#989898':'#000')}"
          >{{ roomSn }}</div>
          <van-icon
            class="w10"
            color='#b2b2b2'
            name="arrow"
          />
        </div>
        <!-- 2.起租日期 -->
        <div
          v-if="active == '0'"
          class="flex mt10"
          @click="show_stime('start')"
        >
          <van-field
            class="w30"
            label="起租日期"
          />
          <div class="w60">{{ dataForm.startTime }}</div>
          <van-icon
            class="w10"
            color='#b2b2b2'
            name="arrow"
          />
        </div>
        <div
          v-if="active == '0' && dataForm.orderType == '4'"
          class="flex"
          :style="{'margin-top':(active == '1' && dataForm.orderType == '4')?'10px':''}"
          @click="show_etime('end')"
        >
          <van-field
            class="w30"
            label="到租日期"
          />
          <div class="w60">{{ d_endTime }}</div>
          <van-icon
            class="w10"
            color='#b2b2b2'
            name="arrow"
          />
        </div>
        <div
          v-if="dataForm.orderType == '3'"
          class="flex"
          :style="{'margin-top':(active == '1' && dataForm.orderType == '3')?'10px':''}"
          @click="show_type('租期')"
        >
          <van-field
            class="w30"
            label="租期"
            red
          />
          <div class="w60">{{ contractPeriod_dictLabel }}<span style="color:#989898;font-size:12px;">{{ jiezhiTime }}</span></div>
          <van-icon
            class="w10"
            color='#b2b2b2'
            name="arrow"
          />
        </div>
        <div
          v-if="dataForm.orderType == '3'"
          class="flex"
          @click="show_type('收租周期')"
        >
          <van-field
            class="w30"
            label="收租周期"
          />
          <div
            class="w60"
            :style="{color:(paymentMethod_dictLabel == '请选择收租周期'?'#989898':'#000')}"
          >{{ paymentMethod_dictLabel }}</div>
          <van-icon
            class="w10"
            color='#b2b2b2'
            name="arrow"
          />
        </div>
        <div
          v-if="dataForm.orderType == '4'"
          :style="{'margin-top':(active == '1' && dataForm.orderType == '4')?'10px':''}"
          class="flex"
        >
          <van-field
            class="w30"
            label="首期入住(天)"
          />
          <van-stepper
            :min='minStepper'
            :max='maxStepper'
            class="w70"
            v-model="dataForm.paymentMethod"
            theme="round"
            button-size="22"
            disable-input
          />
        </div>
        <van-field
          v-model="dataForm.price"
          :label="dataForm.orderType == '4'?'每日租金':'每期租金'"
          placeholder="请输入租金金额"
        />
        <van-field
          v-model="dataForm.deposit"
          label="押金"
          placeholder="请输入押金金额"
        />
        <div v-if="active == '0'">
          <div class="title">入住人信息</div>
          <div
            v-for="(item,i) in info_box"
            :key="i"
            class="addP_box"
          >
            <div>
              <van-field
                :label='item.name_l'
                v-model="item.name_r"
                placeholder="请输入姓名"
              />
              <van-field
                maxlength="18"
                :label='item.ID_l'
                v-model="item.ID_r"
                placeholder="请输入身份证号码"
              />
            </div>
            <div
              class="img_box"
              @click="dealLiver(i)"
              v-if="i != 0"
            >
              <van-icon
                size="20px"
                color="#ff7d41"
                name="clear"
              />
            </div>
          </div>
          <div
            class="addP_style"
            @click="addPenson"
          >+ 添加同住人</div>
        </div>
        <div v-else-if="active == '1'">
          <van-field
            class="mt10"
            v-model="roomUser"
            label="姓名"
            placeholder="请输入姓名"
          />
        </div>
        <van-field
          class="mt10"
          maxlength="11"
          v-model="dataForm.phone"
          label="手机号"
          type="tel"
          placeholder="请输入手机号"
        />
      </van-form>
    </div>
    <!-- bottom -->
    <div
      class="p_bottom"
      @click="onSubmit"
    >
      <van-button
        type="info"
        native-type="submit"
      >{{ bottom_title }}</van-button>
    </div>
    <!-- 弹框 -->
    <van-popup
      v-model="show_types"
      position="bottom"
      :close-on-click-overlay='false'
    >
      <van-picker
        :title="title"
        show-toolbar
        :columns="columns"
        @confirm="onConfirmType"
        @cancel="show_types = false"
      />
    </van-popup>
    <!-- 弹框-基础房型、产品房型、房间号 -->
    <van-popup
      v-model="vir_pro_roomSn_show"
      position="bottom"
      style="overflow:scroll;height:50%;"
    >
      <div class="topTitle">选择{{ title }}</div>
      <div
        class="scale-style"
        v-for="(item,index) in vir_pro_roomSnAll"
        :key="index"
        @click="choose_type(item,index)"
      >
        <div class="scale-left">
          <van-icon
            style="padding-right:10px;"
            :style="{color:(item.imgStatu == true)?'#ff7d41':'#989898'}"
            :name="item.imgStatu == true?'checked':'circle'"
          />
          <div v-if="three_type == 'vir'">{{ item.scaleName }}</div>
          <div v-if="three_type == 'pro'">{{ item.scaleName }}</div>
          <div v-if="three_type == 'roomSn'">{{ item.roomSn }}</div>
        </div>
        <div
          v-if="three_type == 'vir'"
          style="color:#989898;"
        >{{ item.freeNum }}间</div>
      </div>
    </van-popup>
    <!-- 日历弹框 -->
    <van-calendar
      v-model="show_calendar"
      :min-date='minDate'
      :max-date='maxDate'
      @close="show_calendar = false"
      @confirm="onConfirmTime"
    />
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query.js';
import { GetBusinessRoomscaleMonthState, GetBusinessvirtualRoomscaleScaleTypeByRoomScale, PostBusinessOrderExternalSyn, GetBusinessRoomFreeRoomscale } from '@/api/api_business_hotel_and_scale';
import { Toast } from 'vant';
import dayjs from 'dayjs';
import { GetBusinessAccountUnitbyhotel } from '@/api/api_agreement';
export default {
    data() {
        return {
            active: '0',
            username: '',
            value: '',
            sanke: '散客',
            info_box: [
                {
                    name_l: '真实姓名',
                    name_r: '',
                    ID_l: '身份证号',
                    ID_r: ''
                }
            ],
            columns: [],
            customerTypeAll: ['散客', '协议单位'],
            dataForm: {
                hotelId: this.$route.query.hotelId,
                roomScaleId: null,
                virtualRoomScaleId: null,
                roomId: null,
                orderType: '3',
                roomUser: null,
                phone: null,
                roomNum: 1,
                startTime: null,
                paymentMethod: null,
                deposit: null,
                accountUnitId: null,
                price: null,
                lifeCost: null,
                diffDay: null
            },
            roomUser: null,
            sTime: null,
            three_type: null,
            customerType: null,
            bottom_title: '下一步',
            virScaleName: '请选择基础房型',
            proScaleName: '请选择产品房型',
            roomSn: '请选择房间号',
            unitName: null,
            title: null,
            contractPeriod_dictValue: '暂无',
            jiezhiTime: null,
            paymentMethod_dictLabel: '请选择收租周期',
            minStepper: null,
            maxStepper: null,
            d_endTime: '暂无',
            contractPeriod_dictLabel: '暂无',
            minDate: new Date(),
            maxDate: new Date(),
            vir_pro_roomSnAll: [],
            virAll: [],
            show_types: false,
            vir_pro_roomSn_show: false,
            show_calendar: false
        };
    },
    watch: {
        'dataForm.orderType'(val) {
            // 可以监听对象的单个属性
            this.dataForm = {
                hotelId: this.$route.query.hotelId,
                roomScaleId: null,
                virtualRoomScaleId: null,
                roomId: null,
                orderType: '3',
                roomUser: null,
                phone: null,
                roomNum: 1,
                startTime: null,
                paymentMethod: null,
                deposit: null,
                accountUnitId: null,
                price: null,
                lifeCost: null,
                diffDay: null
            };
            this.virScaleName = '请选择基础房型';
            this.proScaleName = '请选择产品房型';
            this.roomSn = '请选择房间号';
            this.contractPeriod_dictLabel = '暂无';
            this.jiezhiTime = null;
            this.paymentMethod_dictLabel = '请选择收租周期';
            this.virAll = [];
            this.vir_pro_roomSnAll = [];
            let c_time = dayjs().format('YYYY-MM-DD HH:mm:ss');
            let c_timeRound_0 = dayjs().format('YYYY-MM-DD') + ' ' + '00:00:00';
            let c_timeRound_8 = dayjs().format('YYYY-MM-DD') + ' ' + '08:00:00';
            if (dayjs(c_time).unix() > dayjs(c_timeRound_0).unix() && dayjs(c_time).unix() < dayjs(c_timeRound_8).unix()) {
                let startMin = dayjs().subtract(1, 'day');
                this.dataForm.startTime = dayjs(startMin).format('YYYY-MM-DD');
            } else {
                this.dataForm.startTime = dayjs().format('YYYY-MM-DD');
            }
            this.dataForm.orderType = val;
        },
        active(neww, olld) {
            this.dataForm = {
                hotelId: this.$route.query.hotelId,
                roomScaleId: null,
                virtualRoomScaleId: null,
                roomId: null,
                orderType: '3',
                roomUser: null,
                phone: null,
                roomNum: 1,
                startTime: null,
                paymentMethod: null,
                deposit: null,
                accountUnitId: null,
                price: null,
                lifeCost: null,
                diffDay: null
            };
            this.virScaleName = '请选择基础房型';
            this.proScaleName = '请选择产品房型';
            this.roomSn = '请选择房间号';
            this.contractPeriod_dictLabel = '暂无';
            this.jiezhiTime = null;
            this.paymentMethod_dictLabel = '请选择收租周期';
            this.virAll = [];
            this.vir_pro_roomSnAll = [];
            let c_time = dayjs().format('YYYY-MM-DD HH:mm:ss');
            let c_timeRound_0 = dayjs().format('YYYY-MM-DD') + ' ' + '00:00:00';
            let c_timeRound_8 = dayjs().format('YYYY-MM-DD') + ' ' + '08:00:00';
            if (dayjs(c_time).unix() > dayjs(c_timeRound_0).unix() && dayjs(c_time).unix() < dayjs(c_timeRound_8).unix()) {
                let startMin = dayjs().subtract(1, 'day');
                this.dataForm.startTime = dayjs(startMin).format('YYYY-MM-DD');
            } else {
                this.dataForm.startTime = dayjs().format('YYYY-MM-DD');
            }
            if (neww == '1') {
                this.bottom_title = '完成办理';
            } else {
                this.bottom_title = '下一步';
            }
        }
    },
    created() {
        this.$store.commit('setHeader', '长短租办理');
        // 0-8之间
        let c_time = dayjs().format('YYYY-MM-DD HH:mm:ss');
        let c_timeRound_0 = dayjs().format('YYYY-MM-DD') + ' ' + '00:00:00';
        let c_timeRound_8 = dayjs().format('YYYY-MM-DD') + ' ' + '08:00:00';
        if (dayjs(c_time).unix() > dayjs(c_timeRound_0).unix() && dayjs(c_time).unix() < dayjs(c_timeRound_8).unix()) {
            let startMin = dayjs().subtract(1, 'day');
            this.dataForm.startTime = dayjs(startMin).format('YYYY-MM-DD');
        } else {
            this.dataForm.startTime = dayjs().format('YYYY-MM-DD');
        }
        this.customerType = this.customerTypeAll[0];
        this.getTypes();
    },
    methods: {
        getTypes() {
            // 字典查询-客户类型
            GetBusinessAccountUnitbyhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                if (res.data.length > 0) {
                    this.customerTypesOldAll = res.data;
                    this.customerTypesAll = res.data.map((item) => item.cooperationName);
                    this.unitName = this.customerTypesOldAll[0].cooperationName;
                    this.dataForm.accountUnitId = this.customerTypesOldAll[0].id;
                }
            });
            // 字典查询-长租合同期限
            GetSystemDictDataByType({ dictType: 'business_contract_period' }).done((res) => {
                if (res.data.length > 0) {
                    this.d_contractPeriodOldAll = res.data;
                }
            });
            // 字典查询-支付方式
            GetSystemDictDataByType({ dictType: 'business_payment_method' }).done((res) => {
                if (res.data.length > 0) {
                    this.d_paymentMethodOldAll = res.data;
                    this.getVirInfo();
                }
            });
        },
        // 基础房型
        getVirInfo() {
            let data = {
                hotelId: this.$route.query.hotelId,
                scaleType: this.dataForm.orderType == '3' ? '4' : '5',
                startTime: this.dataForm.startTime
            };
            GetBusinessRoomscaleMonthState(data).done((res) => {
                if (res.data.length > 0) {
                    this.virAll = res.data.filter((item) => item.freeNum > 0);
                    this.virAll = res.data;
                    for (let i = 0; i < this.virAll.length; i++) {
                        let item = this.virAll[i];
                        if (item.scaleName == this.virScaleName) {
                            item.imgStatu = true;
                        } else {
                            item.imgStatu = false;
                        }
                    }
                    this.vir_pro_roomSnAll = this.virAll;
                } else {
                    Toast({
                        message: '暂无基础房型',
                        duration: 900
                    });
                }
            });
        },
        // 产品房型
        getProInfo() {
            let data = {
                roomScaleId: this.dataForm.roomScaleId,
                scaleType: this.dataForm.orderType == '3' ? '4' : '5'
            };
            GetBusinessvirtualRoomscaleScaleTypeByRoomScale(data).done((res) => {
                if (res.data.length > 0) {
                    this.proAll = res.data;
                    for (let i = 0; i < this.proAll.length; i++) {
                        let item = this.proAll[i];
                        if (item.scaleName == this.proScaleName) {
                            item.imgStatu = true;
                        } else {
                            item.imgStatu = false;
                        }
                    }
                    this.vir_pro_roomSnAll = this.proAll;
                    this.vir_pro_roomSn_show = true;
                } else {
                    this.proAll = [];
                    this.roomSnAll = [];
                    Toast({
                        message: '暂无对应产品房型',
                        duration: 900
                    });
                    this.proScaleName = '暂无';
                    this.roomSn = '暂无';
                }
            });
        },
        // 查询可用房间数量
        getUserRooms() {
            GetBusinessRoomFreeRoomscale(this.dataForm.virtualRoomScaleId).done((res) => {
                if (res.data.length > 0) {
                    this.roomSnAll = res.data;
                    for (let i = 0; i < this.roomSnAll.length; i++) {
                        let item = this.roomSnAll[i];
                        if (item.roomSn == this.roomSn) {
                            item.imgStatu = true;
                        } else {
                            item.imgStatu = false;
                        }
                    }
                    this.vir_pro_roomSnAll = this.roomSnAll;
                    this.vir_pro_roomSn_show = true;
                } else {
                    Toast({
                        message: '当前房型无可选房间,请重新选择',
                        duration: 1000
                    });
                    this.roomSn = '暂无';
                }
            });
        },
        // 展开-类型
        show_type(title) {
            this.title = title;
            if (title == '暂无') {
                return;
            }
            if (title == '客户类型') {
                this.columns = this.customerTypeAll;
                this.show_types = true;
            } else if (title == '单位名称') {
                this.columns = this.customerTypesAll;
                this.show_types = true;
            } else if (title == '基础房型') {
                this.three_type = 'vir';
                if (this.virAll.length == 0) {
                    this.getVirInfo();
                    this.vir_pro_roomSn_show = true;
                } else {
                    this.vir_pro_roomSnAll = this.virAll;
                    this.vir_pro_roomSn_show = true;
                }
            } else if (title == '产品房型') {
                if (this.virScaleName == '请选择基础房型') {
                    Toast({
                        message: '请选择基础房型',
                        duration: 900
                    });
                } else {
                    this.three_type = 'pro';
                    this.getProInfo();
                }
            } else if (title == '房间号') {
                if (this.virScaleName == '请选择基础房型') {
                    Toast({
                        message: '请选择基础房型',
                        duration: 900
                    });
                    return;
                } else if (this.proScaleName == '请选择产品房型') {
                    Toast({
                        message: '请选择产品房型',
                        duration: 900
                    });
                    return;
                } else {
                    this.three_type = 'roomSn';
                    this.getUserRooms();
                }
            } else if (title == '租期') {
                if (this.virScaleName == '请选择基础房型') {
                    Toast({
                        message: '请选择基础房型',
                        duration: 900
                    });
                    return;
                } else if (this.proScaleName == '请选择产品房型') {
                    Toast({
                        message: '请选择产品房型',
                        duration: 900
                    });
                    return;
                } else {
                    this.columns = this.contractPeriodAll;
                    this.show_types = true;
                }
            } else if (title == '收租周期') {
                if (this.virScaleName == '请选择基础房型') {
                    Toast({
                        message: '请选择基础房型',
                        duration: 900
                    });
                    return;
                } else if (this.proScaleName == '请选择产品房型') {
                    Toast({
                        message: '请选择产品房型',
                        duration: 900
                    });
                    return;
                } else {
                    this.columns = this.paymentMethodAll;
                    this.show_types = true;
                }
            }
        },
        // 选择-类型
        onConfirmType(value) {
            if (this.title == '客户类型') {
                this.customerType = value;
                this.show_types = false;
            } else if (this.title == '单位名称') {
                this.unitName = value;
                this.dataForm.accountUnitId = this.customerTypesOldAll.find((item) => item.cooperationName == value).id;
                this.show_types = false;
            } else if (this.title == '租期') {
                this.contractPeriod_dictLabel = value;
                for (const k in this.contractPeriodAllObj) {
                    if (value == this.contractPeriodAllObj[k]) {
                        this.contractPeriod_dictValue = k;
                    }
                }
                this.show_types = false;
                this.dealData(this.contractPeriod_dictValue, this.two_paymentMethod, '选择后');
            } else if (this.title == '收租周期') {
                this.paymentMethod_dictLabel = value;
                this.dataForm.price = this.d_paymentMethodOldAll.find((item) => item.dictLabel == value).price;
                this.dataForm.paymentMethod = this.d_paymentMethodOldAll.find((item) => item.dictLabel == value).dictValue;
                this.show_types = false;
            }
        },
        // 展开-起租日期
        show_stime(type) {
            this.type = type;
            if (this.active == '0') {
                if (this.dataForm.orderType == '3' && this.virScaleName == '请选择基础房型') {
                    Toast({
                        message: '请选择基础房型',
                        duration: 900
                    });
                    return;
                } else if (this.dataForm.orderType == '3' && this.proScaleName == '请选择产品房型') {
                    Toast({
                        message: '请选择产品房型',
                        duration: 900
                    });
                    return;
                } else {
                    // this.minDate = new Date(dayjs(this.dataForm.startTime).subtract(1, 'month'));
                    // this.maxDate = new Date(this.dataForm.startTime);
                    this.minDate = new Date(dayjs().subtract(1, 'month'));
                    this.maxDate = new Date();
                    this.jiezhiTime = '至' + dayjs(this.dataForm.startTime).add(this.contractPeriod_dictValue, 'month').format('YYYY年MM月DD日');
                    this.show_calendar = true;
                }
            } else if (this.active == '1') {
                // this.minDate = new Date(this.dataForm.startTime);
                // this.maxDate = new Date(dayjs(this.dataForm.startTime).add(6, 'month'));
                this.minDate = new Date();
                this.maxDate = new Date(dayjs().add(6, 'month'));
                this.show_calendar = true;
            }
        },
        // 展开-到租日期
        show_etime(type) {
            if (this.virScaleName == '请选择基础房型') {
                Toast({
                    message: '请选择基础房型',
                    duration: 900
                });
                return;
            } else if (this.proScaleName == '请选择产品房型') {
                Toast({
                    message: '请选择产品房型',
                    duration: 900
                });
                return;
            } else {
                this.type = type;
                this.minDate = new Date(dayjs(this.dataForm.startTime).add(Number(this.dataForm.paymentMethod), 'day'));
                this.maxDate = new Date(dayjs(this.d_endTime));
                this.show_calendar = true;
            }
        },
        // 选择-日期
        onConfirmTime(date) {
            if (this.type == 'start') {
                this.dataForm.startTime = dayjs(date).format('YYYY-MM-DD');
                this.show_calendar = false;
                this.jiezhiTime = '至' + dayjs(this.dataForm.startTime).add(this.contractPeriod_dictValue, 'month').format('YYYY年MM月DD日');
                if (this.dataForm.orderType == '4') {
                    this.d_endTime = dayjs(this.dataForm.startTime).add(Number(this.contractPeriod_dictValue), 'month').format('YYYY-MM-DD');
                }
                if (this.active == '1') {
                    this.virScaleName = '请选择基础房型';
                    this.proScaleName = '请选择产品房型';
                    this.roomSn = '请选择房间号';
                    this.contractPeriod_dictLabel = '暂无';
                    this.jiezhiTime = null;
                    this.paymentMethod_dictLabel = '请选择收租周期';
                    this.dataForm.price = null;
                    this.dataForm.deposit = null;
                    this.getVirInfo();
                }
            } else if (this.type == 'end') {
                this.d_endTime = dayjs(date).format('YYYY-MM-DD');
                this.maxStepper = dayjs(this.d_endTime).diff(dayjs(this.dataForm.startTime), 'day');
                this.show_calendar = false;
            }
        },
        // 选择-基础房型、产品房型、房间号
        choose_type(item, index) {
            if (this.three_type == 'vir') {
                this.virScaleName = item.scaleName;
                this.proScaleName = '请选择产品房型';
                this.roomSn = '请选择房间号';
                this.contractPeriod_dictLabel = '暂无';
                this.jiezhiTime = null;
                this.paymentMethod_dictLabel = '请选择收租周期';
                this.dataForm.price = null;
                this.dataForm.deposit = null;
                for (let i = 0; i < this.virAll.length; i++) {
                    if (this.virAll[i].scaleName == item.scaleName) {
                        this.dataForm.roomScaleId = this.virAll[i].id;
                        this.bookMonth = this.virAll[i].bookMonth;
                    }
                    if (index == i) {
                        this.virAll[i].imgStatu = true;
                    } else {
                        this.virAll[i].imgStatu = false;
                    }
                }
                this.vir_pro_roomSn_show = false;
                if (this.virScaleName && this.vir_pro_roomSn_show == false) {
                    this.three_type = 'pro';
                    this.title = '产品房型';
                    this.getProInfo();
                }
            } else if (this.three_type == 'pro') {
                this.proScaleName = item.scaleName;
                this.roomSn = '请选择房间号';
                for (let i = 0; i < this.proAll.length; i++) {
                    if (this.proAll[i].scaleName == item.scaleName) {
                        this.dataForm.virtualRoomScaleId = this.proAll[i].id;
                        this.dataForm.deposit = this.proAll[i].deposit;
                        this.dataForm.lifeCost = this.proAll[i].lifeCost;
                        this.two_paymentMethod = this.proAll[i].paymentMethod;
                        this.realAmount = this.proAll[i].realAmount;
                        this.dealData(this.proAll[i].contractPeriod, this.two_paymentMethod, '');
                    }
                    if (index == i) {
                        this.proAll[i].imgStatu = true;
                    } else {
                        this.proAll[i].imgStatu = false;
                    }
                }
                this.vir_pro_roomSn_show = false;
                if (this.proScaleName && this.vir_pro_roomSn_show == false && this.active == '0') {
                    this.three_type = 'roomSn';
                    this.title = '房间号';
                    this.getUserRooms();
                }
            } else if (this.three_type == 'roomSn') {
                this.roomSn = item.roomSn;
                for (let i = 0; i < this.roomSnAll.length; i++) {
                    if (this.roomSnAll[i].roomSn == item.roomSn) {
                        this.dataForm.roomId = this.roomSnAll[i].id;
                    }
                    if (index == i) {
                        this.roomSnAll[i].imgStatu = true;
                    } else {
                        this.roomSnAll[i].imgStatu = false;
                    }
                }
                this.vir_pro_roomSn_show = false;
            }
        },
        // 处理数据-产品房型产生
        dealData(contractPeriod, paymentMethod, type) {
            if (contractPeriod) {
                if (type == '选择后') {
                    this.contractPeriod_dictValue = contractPeriod;
                    this.jiezhiTime = '至' + dayjs(this.dataForm.startTime).add(this.contractPeriod_dictValue, 'month').format('YYYY年MM月DD日');
                } else {
                    let arr_new = contractPeriod.split(',');
                    let short_hetong = Math.min(...arr_new);
                    let long_hetong = Math.max(...arr_new);
                    this.contractPeriod_dictValue = Math.max(...arr_new);
                    let d_arr = [];
                    let obj = {};
                    for (let i = short_hetong; i <= long_hetong; i++) {
                        d_arr.push(i + '月');
                        obj[i] = i + '月';
                    }
                    this.contractPeriodAll = d_arr;
                    this.contractPeriodAllObj = obj;
                    for (const k in this.contractPeriodAllObj) {
                        if (this.contractPeriod_dictValue == k) {
                            this.contractPeriod_dictLabel = this.contractPeriodAllObj[k];
                        }
                    }
                    this.jiezhiTime = '至' + dayjs(this.dataForm.startTime).add(this.contractPeriod_dictValue, 'month').format('YYYY年MM月DD日');
                }
            }
            if (this.dataForm.orderType == '3' && paymentMethod) {
                let d_paymentMethod = JSON.parse(paymentMethod);
                let arr = [];
                this.d_paymentMethodOldAll.map((item) => {
                    for (const k in d_paymentMethod) {
                        if (k == item.dictValue) {
                            item.price = d_paymentMethod[k];
                            arr.push(item);
                        }
                    }
                });
                this.paymentMethodOldAll = [];
                arr.map((item) => {
                    if (this.contractPeriod_dictValue % item.dictValue == '0') {
                        this.paymentMethodOldAll.push(item);
                    }
                });
                this.paymentMethodAll = this.paymentMethodOldAll.map((item) => item.dictLabel);
                this.paymentMethod_dictLabel = this.paymentMethodOldAll[0].dictLabel;
                this.dataForm.paymentMethod = this.paymentMethodOldAll[0].dictValue;
                this.dataForm.price = this.paymentMethodOldAll[0].price;
            } else if (this.dataForm.orderType == '4' && paymentMethod) {
                this.dataForm.price = this.realAmount;
                this.dataForm.paymentMethod = paymentMethod; // 首期入住
                this.d_endTime = dayjs(this.dataForm.startTime).add(Number(this.contractPeriod_dictValue), 'month').format('YYYY-MM-DD');
                this.maxStepper = dayjs(this.d_endTime).diff(dayjs(this.dataForm.startTime), 'day');
            }
        },
        // 添加-入住人
        addPenson() {
            var info = {
                name_l: '真实姓名',
                name_r: '',
                ID_l: '身份证号',
                ID_r: ''
            };
            this.info_box.push(info);
        },
        // 删除-入住人
        dealLiver(i) {
            this.info_box.splice(i, 1);
        },
        // 下一步
        onSubmit() {
            if (this.virScaleName && this.virScaleName == '请选择基础房型') {
                Toast({
                    message: '请选择基础房型',
                    duration: 900
                });
                return false;
            }
            if ((this.proScaleName && this.proScaleName == '请选择产品房型') || (this.proScaleName && this.proScaleName != '请选择产品房型' && this.roomSn == '暂无')) {
                Toast({
                    message: '请选择产品房型',
                    duration: 900
                });
                return false;
            }
            if (this.active == '0' && this.roomSn && this.roomSn == '请选择房间号') {
                Toast({
                    message: '请选择房间号',
                    duration: 900
                });
                return false;
            }
            if (this.customerType == '散客') {
                delete this.dataForm.accountUnitId;
            }
            if (this.active == '0') {
                let arr = [];
                for (var i = 0; i < this.info_box.length; i++) {
                    if (this.info_box[i].ID_r && !/^[1-9]{1}[0-9]{14}$|^[1-9]{1}[0-9]{16}([0-9]|[xX])$/.test(this.info_box[i].ID_r)) {
                        Toast({
                            message: '请正确填写身份证号码',
                            duration: 900
                        });
                        return false;
                    }
                    let obj = {};
                    obj.roomUser = this.info_box[i].name_r;
                    obj.idCard = this.info_box[i].ID_r;
                    arr.push(obj);
                    this.dataForm.roomUser = JSON.stringify(arr);
                }
            } else {
                this.dataForm.roomUser = this.roomUser;
            }
            if (!this.dataForm.phone || (this.dataForm.phone && !/^1[3-9]\d{9}$/.test(this.dataForm.phone))) {
                Toast({
                    message: '请正确输入11位手机号码',
                    duration: 900
                });
                return false;
            }
            if (this.dataForm.orderType == '3') {
                this.n_d_endTime = dayjs(this.dataForm.startTime).add(this.contractPeriod_dictValue, 'month').format('YYYY-MM-DD');
                // this.dataForm.diffDay = dayjs(this.n_d_endTime).diff(dayjs(this.dataForm.startTime), 'day');
                this.dataForm.diffDay = this.contractPeriod_dictLabel.substring(0, this.contractPeriod_dictLabel.length - 1);
            } else {
                this.dataForm.diffDay = dayjs(this.d_endTime).diff(dayjs(this.dataForm.startTime), 'day');
            }
            if (this.bottom_title == '下一步') {
                let fenfangInfo = {
                    scaleName: this.virScaleName,
                    roomSn: this.roomSn
                };
                this.$router.push({
                    path: '/paySet-page',
                    query: {}
                });
                localStorage.setItem('fenfangInfo', JSON.stringify(fenfangInfo));
                localStorage.setItem('live_next', JSON.stringify(this.dataForm));
            } else {
                let orderInfoMore = {
                    hotelId: this.$route.query.hotelId,
                    roomScaleId: this.dataForm.roomScaleId,
                    virtualRoomScaleId: this.dataForm.virtualRoomScaleId,
                    orderType: this.dataForm.orderType,
                    phone: this.dataForm.phone,
                    roomNum: '1',
                    diffDay: this.dataForm.diffDay,
                    startTime: this.dataForm.startTime,
                    accountUnitId: this.dataForm.accountUnitId,
                    paymentMethod: this.dataForm.paymentMethod,
                    deposit: this.dataForm.deposit,
                    price: this.dataForm.price,
                    roomUser: this.active == '1' ? this.roomUser : this.dataForm.roomUser
                };
                PostBusinessOrderExternalSyn(orderInfoMore).done((res) => {
                    Toast({
                        message: res.msg,
                        duration: 900
                    });
                    setTimeout('window.history.go(-1)', 900);
                });
            }
        }
    }
};
</script>
<style scoped>
#scroll-orderMonth {
    position: absolute;
    top: 50px;
    bottom: 60px;
    left: 0;
    right: 0;
    overflow-y: scroll;
    font-size: 14px;
    color: #4a4a4a;
}
.flex {
    display: flex;
    align-items: center;
    background: #fff;
    border-bottom: 1px solid #f7f7f7;
}
.title {
    text-align: left;
    padding: 10px 14px;
    font-weight: bold;
    color: #000;
}
.addP_box {
    display: flex;
    background: #fff;
    align-items: center;
    justify-content: space-between;
    padding-right: 25px;
}
.addP_style {
    color: #ff7d41;
    text-align: center;
    padding: 10px;
    background: #fff;
}
.p_bottom {
    position: fixed;
    width: 100%;
    left: 0;
    bottom: 0;
}
.topTitle {
    padding: 10px;
    font-weight: bold;
    font-size: 15px;
    border-bottom: 1px solid #989898;
}
.scale-left {
    display: flex;
    align-items: center;
}
.scale-style {
    display: flex;
    justify-content: space-between;
    padding: 16px 10px;
    align-items: center;
    border-bottom: 1px solid #eee;
}
.mt10 {
    margin-top: 10px;
}
.w30 {
    width: 30%;
    text-align: left;
}
.w70 {
    width: 70%;
    text-align: left;
}
.w60 {
    width: 60%;
    text-align: left;
}
.w10 {
    width: 10%;
}
::v-deep .van-tabs__line {
    width: 115px;
    height: 2px;
    background-color: #ff7d41;
}
::v-deep .van-radio__icon--checked .van-icon {
    background-color: #ff7d41;
    border-color: #ff7d41;
}
::v-deep .van-stepper--round .van-stepper__minus {
    color: #ff7d41;
    border: 1px solid #ff7d41;
}
::v-deep .van-stepper--round .van-stepper__plus {
    background: #ff7d41;
}
::v-deep .van-button--info {
    border: 1px solid #ff7d41;
    background: #ff7d41;
    width: 100%;
}
::v-deep .van-radio__icon {
    font-size: 15px;
}
</style>



